body {
  font-family: "Roboto", sans-serif;
}

a {
  color: black;
  text-decoration: none;
}

p, a {
  font-size: 23px;
}

li {
  list-style: none;
  padding: 0px;
  width: 0px;
}

h1 {
  font-size: 80px;
}

h2 {
  font-size: 35px;
}

h3 {
  font-size: 30px;
}

.formato {
  width: 90%;
  margin: auto;
}

.bold {
  font-weight: 700;
}

.boton {
  color: white;
  padding: 5px 15px;
  border-radius: 20px;
  border: 1px black solid;
}
.boton a {
  color: black;
}

.boton:hover {
  padding: 6px 16px;
  background-color: #5E17EB;
  border: 0px solid;
}

.boton:hover a {
  color: white;
}

.contenedor {
  width: 75%;
  margin: auto;
}

.morado {
  color: #5E17EB;
}

@media (min-width: 0px) and (max-width: 480px) {
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 26px;
  }
  h3 {
    font-size: 20px;
  }
  p {
    font-size: 17px;
  }
  .formato {
    width: 100%;
  }
}
@media (min-width: 479px) and (max-width: 768px) {
  p {
    font-size: 17px;
  }
}
.cabecera {
  width: 100%;
  height: 550px;
  background-image: url("../../../build/img/imagenHome.webp");
  background-repeat: no-repeat;
  background-size: cover;
  filter: saturate(150%);
  color: white;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.cabecera h2,
.cabecera h1,
.cabecera h3 {
  margin: 0px;
  padding: 0px;
}
.cabecera h2,
.cabecera h3 {
  font-weight: 300;
}
.cabecera .glass {
  margin-right: 50px;
  padding: 0px 50px;
  width: auto;
  height: 350px;
  background: rgba(117, 195, 255, 0.21);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(50px);
  -webkit-backdrop-filter: blur(8.9px);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0px;
}
.cabecera .texto {
  height: 90%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: end;
  padding-right: 50px;
  overflow: hidden;
}
.cabecera .texto h1, .cabecera .texto h2, .cabecera .texto h3 {
  transform: translateX(600px);
  opacity: 0;
}
.cabecera .transicionTexto h1, .cabecera .transicionTexto h2, .cabecera .transicionTexto h3 {
  transition: opacity 2s 1s, transform 2s 1s;
  transform: translateX(0px);
  opacity: 1;
}
.cabecera .linea {
  display: flex;
  align-items: center;
  height: 0px;
  width: 6px;
  border-radius: 30px;
  background-color: white;
  transition: height 1s;
}
.cabecera .transicionLinea {
  transition: height 1s;
  display: flex;
  align-items: center;
  height: 250px;
  width: 6px;
  border-radius: 30px;
  background-color: white;
}
@media (min-width: 0px) and (max-width: 480px) {
  .cabecera .glass {
    width: 80%;
    margin: auto;
    padding: 0 20px;
  }
  .cabecera .texto {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: -30px;
    text-align: center;
  }
  .cabecera .linea {
    width: 0px;
  }
  .cabecera h1 {
    font-size: 40px;
  }
  .cabecera h2 {
    font-size: 26px;
  }
  .cabecera h3 {
    font-size: 20px;
  }
}
@media (min-width: 479px) and (max-width: 768px) {
  .cabecera .linea {
    margin-left: 20px;
  }
}

.navegacion {
  margin-top: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 350px;
}
.navegacion img {
  width: 100px;
}
.navegacion > div {
  display: flex;
  align-items: center;
  gap: 50px;
}
.navegacion > div .youtube {
  font-weight: 300;
  padding: 10px;
}
.navegacion > div .youtube:hover {
  font-weight: 500;
  padding-top: 4px;
  padding-bottom: 2px;
  border-bottom: 2px solid #5E17EB;
}

@media (min-width: 479px) and (max-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: column;
  }
  .navegacion > div {
    width: 70%;
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
}
@media (min-width: 0px) and (max-width: 480px) {
  .navegacion {
    display: flex;
    flex-direction: column;
  }
  .navegacion > div {
    width: 100%;
    padding-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
}
.bienvenida {
  margin: 130px 0px;
  text-align: center;
}
.bienvenida h2 {
  font-size: 50px;
}
.bienvenida h3 {
  font-weight: 300;
}

.presentacion {
  border-radius: 30px;
  background-color: #5E17EB;
  color: white;
  padding: 50px 0px;
}
.presentacion h2 {
  font-size: 45px;
  font-weight: 500;
}
.presentacion p {
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 35px;
}
@media (min-width: 0px) and (max-width: 480px) {
  .presentacion {
    border-radius: 0px;
  }
  .presentacion h2 {
    text-align: center;
  }
  .presentacion p {
    line-height: normal;
  }
}
@media (min-width: 479px) and (max-width: 768px) {
  .presentacion {
    border-radius: 30px;
  }
}

.datos {
  margin: 75px 0px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
}
.datos div {
  width: 35%;
}
.datos h2 {
  font-size: 55px;
  margin-bottom: -10px;
}
.datos h3 {
  padding-top: 0px;
  font-size: 35px;
  font-weight: 300;
}
@media (min-width: 479px) and (max-width: 768px) {
  .datos {
    display: flex;
    flex-direction: column;
  }
  .datos div {
    width: 70%;
  }
}

.tecnologiasProyectos {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}
.tecnologiasProyectos .subtitulo {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: right;
  color: white;
  width: 100%;
  height: 300px;
  border-radius: 30px;
  box-shadow: 0px 0px 20px black;
  margin: 20px 0px;
  background: rgb(1, 35, 60);
}
.tecnologiasProyectos .subtitulo h2 {
  font-size: 70px;
  font-weight: 500;
  margin-right: 40px;
  text-align: center;
}
.tecnologiasProyectos .subtitulo .linea {
  display: flex;
  align-items: center;
  height: 170px;
  width: 5px;
  border-radius: 30px;
  background-color: white;
}

.carrusel {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.imagenes {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: end;
}
.imagenes .img {
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.75);
  height: 576px;
  width: 1134px;
  border-radius: 15px;
  margin: 5px;
  object-fit: cover;
  filter: saturate(175%);
}
.imagenes .texto {
  overflow: hidden;
  position: absolute;
  flex-direction: column;
  transform: translateY(0px);
  margin-bottom: 9px;
  backdrop-filter: blur(20px);
  background-color: rgba(63, 106, 138, 0.21);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 30px;
}
.imagenes h3 {
  text-shadow: 0px 0px 15px #000000;
  padding-top: 40px;
  color: white;
  font-weight: 300;
}
.imagenes p {
  text-align: center;
  padding: 20px;
  color: white;
  font-size: 0px;
  font-weight: 300;
}

.imagenes .texto {
  width: 600px;
  height: 100px;
  transition: 1s;
}

.imagenes:hover .texto {
  transition: height 1s, transform 1s;
  transform: translateY(-150px);
  height: 300px;
}
.imagenes:hover .texto p {
  transition: font-size 0s 0.2s linear;
  font-size: 23px;
  font-weight: 300;
  text-shadow: 0px 0px 10px #000000;
}

.puntos {
  display: flex;
  align-items: center;
  justify-content: center;
}
.puntos p {
  margin-top: 0px;
  font-size: 100px;
  font-weight: 500;
  margin-top: -50px;
}
.puntos .bold {
  font-weight: 700;
  margin-left: 10px;
  margin-right: 10px;
  color: #5E17EB;
}

.estudios {
  padding-top: 100px;
  background-color: aliceblue;
  padding-bottom: 100px;
}

.contendorEstudio {
  display: flex;
  flex-direction: column;
  width: 75%;
  margin: 50px auto;
  padding: 20px 0px;
  overflow: hidden;
  gap: 50px;
}

.backgroundDinamico {
  display: flex;
}

.estudio {
  height: auto;
  background-color: white;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  display: flex;
  position: relative;
  z-index: 1;
}
.estudio .lineaRosada,
.estudio .lineaMorada,
.estudio .lineaAzul,
.estudio .lineaVerde,
.estudio .lineaDorada {
  padding: 60px 0px;
  width: 15px;
  height: auto;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
}
.estudio .lineaVerde {
  background-color: #54B435;
}
.estudio .lineaMorada {
  background-color: #5E17EB;
}
.estudio .lineaDorada {
  background-color: #DDB758;
}
.estudio .lineaRosada {
  background-color: #EE276A;
}
.estudio .lineaAzul {
  background-color: #4D89F4;
}
.estudio p {
  text-align: left;
}
.estudio .imagenEstudio {
  display: flex;
  align-items: center;
}
.estudio .imagenEstudio .logosEstudio img {
  height: 100px;
  width: 250px;
  object-fit: contain;
}
.estudio .nombre {
  align-self: center;
}
.estudio .nombre p {
  font-size: 20px;
  width: 475px;
  margin: 0px;
}
.estudio .nombre .institucion {
  font-weight: 700;
  letter-spacing: 1px;
  font-size: 27px;
  padding-bottom: 10px;
}
.estudio .estado {
  align-self: center;
  justify-self: center;
}
.estudio .estado p {
  width: 150px;
  margin-left: 30px;
  font-weight: 300;
}

.elemento {
  overflow: hidden;
  width: 95%;
  border-radius: 30px;
  margin: auto;
}

.unal .desplegado {
  background-color: #54B435;
}

.google .desplegado {
  background-color: #4D89F4;
}

.udemy .desplegado {
  background-color: #5E17EB;
}

.politecnico .desplegado {
  background-color: #DDB758;
}

.mintic .desplegado {
  background-color: #EE276A;
}

.desplegado {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  transition: 2s 0s;
}
.desplegado .lineaRosada,
.desplegado .lineaMorada,
.desplegado .lineaAzul,
.desplegado .lineaVerde,
.desplegado .lineaDorada {
  width: 0px;
}
.desplegado .nombre,
.desplegado .institucion {
  color: white;
}
.desplegado .imagenEstudio .logosEstudio img {
  width: 0px;
}
.desplegado .estado p {
  font-size: 0px;
}
.desplegado .masInfoEstudio img {
  filter: invert(100%);
}

.masInfoEstudio {
  align-self: center;
  justify-self: center;
  cursor: pointer;
  width: 30px;
  height: 30px;
}
.masInfoEstudio img {
  width: 30px;
  height: 30px;
}

.ocultar {
  height: 0px;
  transform: translateY(-400px);
  transition: transform 2s, height 2s;
}
.ocultar p,
.ocultar li {
  font-size: 20px;
  width: auto;
  font-size: 0px;
  transition: 0s 2s;
}
.ocultar img {
  width: 0px;
  height: auto;
  transition: 0s 2s;
}
.ocultar .texto a {
  font-size: 0px;
  transition: 0s 2s;
}
.ocultar .texto p,
.ocultar .texto ul {
  width: 0px;
  transition: 0s 2s;
}

.mostrar {
  height: 350px;
  opacity: 1;
  transform: translateY(0px);
  transition: transform 2s, height 2s, padding-bottom 1s 0s;
}
.mostrar img {
  width: 450px;
  height: auto;
}
.mostrar a {
  font-size: 25px;
  font-weight: 500;
}
.mostrar p,
.mostrar li {
  font-size: 20px;
  width: auto;
}
.mostrar .texto p,
.mostrar .texto ul {
  width: 450px;
}
.mostrar .texto a:hover {
  font-weight: 700;
}

.mostrar,
.ocultar {
  width: auto;
  position: relative;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 0px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}
.mostrar li,
.ocultar li {
  list-style: disc;
  padding-bottom: 10px;
}
.mostrar .texto,
.ocultar .texto {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

