.estudios {
    padding-top: 100px;
    background-color: aliceblue;
    padding-bottom: 100px;
}

.contendorEstudio {

    display: flex;
    flex-direction: column;
    width: 75%;
    margin: 50px auto;
    padding: 20px 0px;
    overflow: hidden;
    gap: 50px;
}

.backgroundDinamico {
    display: flex;
}


.estudio {

    height: auto;
    background-color: white;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;


    display: flex;



    position: relative;
    z-index: 1;


    .lineaRosada,
    .lineaMorada,
    .lineaAzul,
    .lineaVerde,
    .lineaDorada {
        padding: 60px 0px;
        width: 15px;
        height: auto;
        border-top-left-radius: 30px;
        border-bottom-left-radius: 30px;
    }

    .lineaVerde {
        background-color: #54B435;
    }

    .lineaMorada {
        background-color: $morado;
    }

    .lineaDorada {
        background-color: #DDB758;
    }

    .lineaRosada {
        background-color: #EE276A;
    }

    .lineaAzul {
        background-color: #4D89F4;
    }

    p {
        text-align: left;
    }

    .imagenEstudio {

        display: flex;
        align-items: center;

        .logosEstudio img {
            height: 100px;
            width: 250px;
            object-fit: contain;
        }

    }

    .nombre {

        align-self: center;

        p {
            font-size: 20px;
            width: 475px;
            margin: 0px;

        }

        .institucion {
            font-weight: 700;
            letter-spacing: 1px;
            font-size: 27px;
            padding-bottom: 10px;
        }
    }

    .estado {
        align-self: center;
        justify-self: center;

        p {
            width: 150px;
            margin-left: 30px;
            font-weight: 300;
        }

    }

}

.elemento {
    overflow: hidden;
    width: 95%;
    border-radius: 30px;
    margin: auto;
}

.unal .desplegado {
    background-color: #54B435;
}

.google .desplegado {
    background-color: #4D89F4;
}

.udemy .desplegado {
    background-color: $morado;
}

.politecnico .desplegado {
    background-color: #DDB758;
}

.mintic .desplegado {
    background-color: #EE276A;
}

.desplegado {
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 0px;
    transition: 2s 0s;


    .lineaRosada,
    .lineaMorada,
    .lineaAzul,
    .lineaVerde,
    .lineaDorada {
        width: 0px;
    }

    .nombre,
    .institucion {
        color: white;
    }

    .imagenEstudio .logosEstudio img {
        width: 0px;
    }

    .estado p {
        font-size: 0px;
    }

    .masInfoEstudio {
        img {
            filter: invert(100%);
        }
    }
}

.masInfoEstudio {
    align-self: center;
    justify-self: center;
    cursor: pointer;
    width: 30px;
    height: 30px;

    img {
        width: 30px;
        height: 30px;
    }

}

.ocultar {
    height: 0px;
    transform: translateY(-400px);
    transition: transform 2s, height 2s;

    p,
    li {
        font-size: 20px;
        width: auto;
        font-size: 0px;
        transition: 0s 2s;
    }

    img {
        width: 0px;
        height: auto;
        transition: 0s 2s;
    }


    .texto {
        a {
            font-size: 0px;
            transition: 0s 2s;
        }

        p,
        ul {
            width: 0px;
            transition: 0s 2s;
        }
    }
}

.mostrar {
    height: 350px;
    opacity: 1;
    transform: translateY(0px);
    transition: transform 2s, height 2s, padding-bottom 1s 0s;

    img {
        width: 450px;
        height: auto;
    }
    a {
        font-size: 25px;
        font-weight: 500;
    }

    p,
    li {
        font-size: 20px;
        width: auto;
    }

    .texto {

        p,
        ul {
            width: 450px;
        }

        a:hover {
            font-weight: 700;
        }
    }

}

.mostrar,
.ocultar {
    width: auto;
    position: relative;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-bottom: 0px;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;


    li {
        list-style: disc;
        padding-bottom: 10px;
    }

    .texto {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
}


.verde {
    color: #54B435;
}

.azul {
    color: #4D89F4;
}

.dorado {
    color: #DDB758;
}

.rosado {
    color: #EE276A;
}

.subtitulo {
    @include subtitulo(5px, 170px)
}