@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

body {
    font-family: 'Roboto', sans-serif;
    // background-color: rgb(37, 37, 81);
}

a{
    color: black;
    text-decoration: none;
}

p,a{
    font-size: 23px;
}

li{
    list-style: none;
    padding: 0px;
    width: 0px;
}


h1 {
    font-size: 80px;
}

h2 {
    font-size: 35px;
}

h3 {
    font-size: 30px;
}

.formato{
    width: 90%;
    margin: auto;
}

.bold {
    font-weight: 700;
}

.boton{
    color:white;
    padding: 5px 15px;
    border-radius: 20px;
    border: 1px black solid;
    a{
        color: black;
    }
   

}

.boton:hover{
    padding: 6px 16px;
    background-color: $morado;
    border: 0px solid;
}
.boton:hover a{
    color:white;
}

.contenedor{
    width: 75%;
    margin: auto;
}

.morado {
    color: $morado;
}




@include telefono{
    h1 {
        font-size: 40px;
    }

    h2 {
        font-size: 26px;
    }

    h3 {
        font-size: 20px;
    }

    p{
        font-size: 17px;
    }

    .formato{
        width: 100%;
    }
}

@include tablet{

    p{
        font-size: 17px;
    }

}