@mixin telefono {
    @media (min-width: 0px) and (max-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: 479px) and (max-width: #{$tablet}) {
        @content;
    }
}


@mixin desktop {
    @media (min-width: 766px) and (max-width: #{$desktop}) {
        @content;
    }
}

@mixin linea($w, $h) {
    display: flex;
    align-items: center;
    height: $h ;
    width: $w ;
    border-radius: 30px;
    background-color: white;

}

@mixin subtitulo($w, $h) {

    display: flex;
    align-items: center;
    justify-content: center;
    text-align: right;
    color: white;

    width: 100%;
    height: 300px;
    border-radius: 30px;
    box-shadow: 0px 0px 20px black;
    margin: 20px 0px ;

    background: rgb(1, 35, 60);

    h2 {
        font-size: 70px;
        font-weight: 500;
        margin-right: 40px;
        text-align: center;
    }

    .linea {
        @include linea($w, $h)
    }

}