.cabecera {

    width: 100%;
    height: 550px;

    background-image: url('../../../build/img/imagenHome.webp');
    background-repeat: no-repeat;
    background-size: cover;
    filter: saturate(150%);

    color: white;

    display: flex;
    align-items: center;
    justify-content: flex-end;



    h2,
    h1,
    h3 {
        margin: 0px;
        padding: 0px;
    }

    h2,
    h3 {
        font-weight: 300;
    }
 

    .glass {
        margin-right: 50px;
        padding: 0px 50px;

        width: auto;
        height: 350px;

        background: rgba(117, 195, 255, 0.21);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(50px);
        -webkit-backdrop-filter: blur(8.9px);

        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 0px;
    }

    .texto {
        height: 90%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        text-align: end;
        padding-right: 50px;
        overflow: hidden;
        h1, h2, h3{
            transform : translateX(600px); 
            opacity: 0;
        }
       
    }
    .transicionTexto{
        h1, h2, h3{
            transition:opacity 2s 1s,transform  2s 1s;
            transform : translateX(0px); 
            opacity: 1;
        }
    }

    .linea {
        @include linea(6px, 0px);
        transition: height 1s;
    }

    .transicionLinea{
        transition: height 1s;
        @include linea(6px, 250px);
    }


    @include telefono {
        .glass {
            width: 80%;
            margin:auto;
            padding: 0 20px;
        }

        .texto {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            margin-top: -30px;
            text-align: center;
        }

        .linea {
            width: 0px;
            
        }

        h1 {
            font-size: 40px;
        }

        h2 {
            font-size: 26px;
        }

        h3 {
            font-size: 20px;
        }
    }

    @include tablet {
        .linea {
            margin-left: 20px;
        }
    }
}

