footer {
    background-color: $morado;
    height: 500px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;


    a {
        color: white;
    }

    ul {
        margin: 0px;
        padding-left: 15px;
    }
}

.headerFooter {
    padding-top: 20px;
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: start;

    img {
        filter: invert(100%);
        width: 100px;
    }

    a {
        font-size: 30px;
    }
}

.bodyFooter {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;

    h3 {
        font-size: 27px;
        margin-bottom: 20px;
    }

    a,
    li {
        margin-bottom: 5px;
        font-size: 23px;
        font-weight: 300;
    }

}

.footerFooter {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    justify-content: space-around;
    padding-top: 20px;
    padding-bottom: 20px;
    gap: 30px;

    .lineaFooter {
        @include linea(60%, 4px);
    }

    .copyright {
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: space-around;
        font-weight: 300;
    }
}