.navegacion {
    margin-top: 50px;

    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 350px;

    img {
        width: 100px;
    }

    &>div {
        display: flex;
        align-items: center;
        gap: 50px;

        .youtube {
            font-weight: 300;
            padding: 10px;
        }

        .youtube:hover{
            font-weight: 500;
            padding-top: 4px;
            padding-bottom: 2px;
            border-bottom: 2px solid $morado;
        }
    }

}

@include tablet {
    .navegacion {
        display: flex;
        flex-direction: column;

        &>div{
            width: 70%;
            padding-top: 20px;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
        }
    }
}
@include telefono {
    .navegacion {
        display: flex;
        flex-direction: column;

        &>div{
            width: 100%;
            padding-top: 20px;
            display: flex;
            align-items: center;
            justify-content: space-evenly;
        }
    }
}