.bienvenida {
    margin: 130px 0px;
    text-align: center;

    h2 {
        font-size: 50px;

    }

    h3 {
        font-weight: 300;
    }

}

.presentacion {
    border-radius: 30px;
    background-color: $morado;
    color: white;
    padding: 50px 0px;
    
    h2 {
        font-size: 45px;
        font-weight: 500;
    }

    p {
        font-weight: 300;
        letter-spacing: 1px;
        line-height: 35px;
    }

    @include telefono {
        border-radius: 0px;

        h2 {
            text-align: center;
        }

        p {
            line-height: normal;
        }

    }

    @include tablet {
        border-radius: 30px;
    }
}

.datos {
    margin: 75px 0px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    text-align: center;
  
    div{
        width: 35%;
    }
    
    h2{
        font-size: 55px;
        margin-bottom: -10px;
        
    }
    h3{
        padding-top: 0px;
        font-size: 35px;
        font-weight: 300;
    }
   
    @include tablet{
        display: flex;
        flex-direction: column;
        div{
            width: 70%;
        }
    }
}