.tecnologiasProyectos {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 50px;


    .subtitulo {
        @include subtitulo(5px, 170px)
    }

}

.carrusel {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;

}


.imagenes {

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;

    .img {
        box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.75);
        height: 576px;
        width: 1134px;
        border-radius: 15px;
        margin: 5px;
        object-fit: cover;
        filter: saturate(175%);
    }

    .texto {

        overflow: hidden;
        position: absolute;
        flex-direction: column;
        transform: translateY(0px);
        margin-bottom: 9px;
        backdrop-filter: blur(20px);
        background-color: rgba(63, 106, 138, 0.21);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 30px;
    }

    h3 {
        text-shadow: 0px 0px 15px #000000;
        padding-top: 40px;
        color: white;
        font-weight: 300;
    }

    p {
        text-align: center;
        padding: 20px;
        color: white;
        font-size: 0px;
        font-weight: 300;
    }


}

.imagenes .texto {
    width: 600px;
    height: 100px;
    transition: 1s;
}


.imagenes:hover .texto {
    transition: height 1s, transform 1s;
    transform: translateY(-150px);
    height: 300px;

    p {
        transition: font-size 0s .2s linear;
        font-size: 23px;
        font-weight: 300;

        text-shadow: 0px 0px 10px #000000;
    }

}




.puntos {
    display: flex;
    align-items: center;
    justify-content: center;

    p {
        margin-top: 0px;
        font-size: 100px;
        font-weight: 500;
        margin-top: -50px;

    }

    .bold {
        font-weight: 700;
        margin-left: 10px;
        margin-right: 10px;
        color: $morado;
    }

}